
import { computed, defineComponent } from 'vue';
import { useQuery, useMutation } from '@vue/apollo-composable';
import { useRouter } from 'vue-router';

// components
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonCardContent,
  IonCardHeader,
  IonCardSubtitle,
  IonCardTitle,
  IonIcon,
  IonLabel,
  IonChip,
  IonButtons,
  IonBackButton,
  alertController,
} from '@ionic/vue';

// hooks
import { gamesHook } from '@/hooks/api/fetchGames.hook';

// graphql
import gameQuery from '@/graph/queries/game.query.graphql';
import removeGameMutation from '@/graph/mutations/deleteGame.mutation.graphql';

export default defineComponent({
  name: 'Home',
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonCardContent,
    IonCardSubtitle,
    IonCardTitle,
    IonCardHeader,
    IonChip,
    IonLabel,
    IonIcon,
    IonButtons,
    IonBackButton,
  },
  setup() {
    const { currentRoute, back } = useRouter();
    const gameId = currentRoute.value.params.id;

    const { result } = useQuery(gameQuery, {
      id: gameId,
    });

    const { mutate } = useMutation(removeGameMutation, {
      variables: { game: gameId },
    });

    const game = computed(() => result?.value?.game || null);

    const handleRemoveGame = async () => {
      const alert = await alertController.create({
        header: 'Remove game',
        message: 'Are you sure you wanna remove this game?',
        buttons: [
          {
            text: 'Cancel',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Okay',
            handler: async () => {
              await mutate({ game: gameId });

              // await refetch();
              back();
            },
          },
        ],
      });

      await alert.present();
    };

    return {
      game,
      handleRemoveGame,
    };
  },
  computed: {
    baseUrl: () => process.env.VUE_APP_API_URL,
    gameImage: ({ game, baseUrl }) =>
      game.picture ? `${baseUrl}${game.picture?.url}` : null,
  },
});
